import { Suspense, lazy } from 'preact/compat';
import { Container } from '@adobe/elsie/lib';
import { useMemo, useState } from 'preact/hooks';
import { AuthCombineProps, activeComponentType } from '@/auth/types';
import SkeletonLoader from '@/auth/components/SkeletonLoader';

const ResetPasswordForm = lazy(
  () => import('@/auth/components/ResetPasswordForm')
);
const SignInForm = lazy(() => import('@/auth/components/SignInForm'));
const SignUpForm = lazy(() => import('@/auth/components/SignUpForm'));

export const AuthCombine: Container<AuthCombineProps> = ({
  defaultView = 'signInForm',
  signInFormConfig,
  signUpFormConfig,
  resetPasswordFormConfig,
}) => {
  const [activeComponent, setActiveComponent] =
    useState<activeComponentType>(defaultView);

  const renderComponent = useMemo(
    () => ({
      signInForm: (
        <SignInForm
          setActiveComponent={setActiveComponent}
          {...signInFormConfig}
        />
      ),
      signUpForm: (
        <SignUpForm
          setActiveComponent={setActiveComponent}
          {...signUpFormConfig}
        />
      ),
      resetPasswordForm: (
        <ResetPasswordForm
          setActiveComponent={setActiveComponent}
          {...resetPasswordFormConfig}
        />
      ),
    }),
    [
      resetPasswordFormConfig,
      signInFormConfig,
      signUpFormConfig,
      setActiveComponent,
    ]
  );

  return (
    <Suspense fallback={<SkeletonLoader activeSkeleton={activeComponent} />}>
      <div className="auth-combine">{renderComponent[activeComponent]}</div>
    </Suspense>
  );
};
